.container {
  padding-right: 15px;
  padding-left: 15px;
  max-width: 1140px;
  margin: 0 auto;
}

.row {
  margin-left: -15px;
  margin-right: -15px;
}

.row [class^="col-"] {
  box-sizing: border-box;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 30px;
}

@media (min-width: 768px) {
  .row {
    display: flex;
  }
  .row .col-1-12 {
    width: 8.33333%;
  }
  .row .col-2-12 {
    width: 16.66667%;
  }
  .row .col-3-12 {
    width: 25%;
  }
  .row .col-4-12 {
    width: 33.33333%;
  }
  .row .col-5-12 {
    width: 41.66667%;
  }
  .row .col-6-12 {
    width: 50%;
  }
  .row .col-7-12 {
    width: 58.33333%;
  }
  .row .col-8-12 {
    width: 66.66667%;
  }
  .row .col-9-12 {
    width: 75%;
  }
  .row .col-10-12 {
    width: 83.33333%;
  }
  .row .col-11-12 {
    width: 91.66667%;
  }
  .row .col-12-12 {
    width: 100%;
  }
}

